<template>
  <div>
    <div class="header"><h1 class="text-5xl font-semibold text-center hover:text-blue-500">Europa</h1></div>
    <div class="grid grid-cols-4 gap-4 p-4">
      <div v-for="(item, index) in resultados" :key="index">
        <div class="rounded shadow-md relative">
        <div class="p-4"><!-- TwoLetterSymbol -->
          <p class="text-xl font-semibold">{{item.Country}}  -  Rango: {{item.rank}}</p>
          <p>Riesgo de infección: {{item.Infection_Risk}}%</p>
          <p>Tasa de letalidad: {{item.Case_Fatality_Rate}}</p>
          <p>Porcentaje de prueba: {{item.Test_Percentage}}</p>
          <p>Proporcion de Recuperacion: {{item.Recovery_Proporation}}%</p>
          <hr>
          <p>Total de muertes: {{item.TotalDeaths / 1000}}</p>
          <p>Total de recuperados: {{new Intl.NumberFormat().format(item.TotalRecovered)}}</p>
          <hr>
          <p>Casos activos: {{new Intl.NumberFormat().format(item.ActiveCases)}}</p>
          <p>Casos criticos: {{new Intl.NumberFormat().format(item.Serious_Critical)}}</p>
          <p>Poblacion: {{new Intl.NumberFormat().format(item.Population)}}</p>
        </div>
        <img :src="`http://www.geognos.com/api/en/countries/flag/${convertirAMayusculas(item.TwoLetterSymbol)}.png`" :alt="`${item.Country}`" class="w-full">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Europa',
    props: {
    },
    components: {
    },
    data(){
      return {
        resultados: "",
        responseAvailable: false,
        apikey: '279f4872eamsh3c8efddee56994dp1d8494jsn89d87bbd5c35',
      }
    },
    mounted(){
      this.obtenerData()
    },
    methods: {
      obtenerData(){
        fetch("https://vaccovid-coronavirus-vaccine-and-treatment-tracker.p.rapidapi.com/api/npm-covid-data/europe", {
          "method": "GET",
          "headers": {
            "x-rapidapi-key": this.apikey,
            "x-rapidapi-host": "vaccovid-coronavirus-vaccine-and-treatment-tracker.p.rapidapi.com"
          }
        })
        .then(response => {
          if (response.ok) {
            return response.json()
          }else {
            console.error('Ha ocurrido un error');
          }
        })
        .then(response => {
          console.log(response);
          this.resultados = response
          this.responseAvailable = true
        })
        .catch(err => {
          console.error(err);
        });
      },
      convertirAMayusculas(text){
        return text.toUpperCase()
      }
    }
  }
</script>

